body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

  background-image: url(./images/bg1280x800.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  display: block;
  max-width: 200px;
  height: auto;
}

.card-container {
  display: flex;
  justify-content: center;
}

.card {
  width: 100%;
  margin: 20px;
  display: block;
  padding: 20px;
  padding-top: 0;
  max-width: 400px;
  border: 2px solid rgba(109, 109, 109, 0.473);
  border-radius: 4px;
  box-shadow: 2px 2px 4px 4px rgba(112, 128, 144, 0.158);
  background-color: rgba(0, 0, 0, 0.95);
  color: wheat;
}

.card-info {
  display: flex;
}

.title {
  font-weight: 500;
}

.country-name {
  margin-bottom: 20px;
  text-align: center;
  border-radius: 4px;
  background-color: rgba(245, 159, 10, 0.413);
  font-weight: 500;
}

.country-name-text {
  padding: 10px 0;
}

.image {
  margin: auto;
  height: 100%;
  border: 1px solid rgba(109, 109, 109, 0.2);
}

.card-body {
  margin-right: 20px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
}

.form-input {
  width: 280px;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(245, 159, 10, 0.413);
  height: 70px;
}

h1 {
  color: aliceblue;
  text-align: center;
  font-style: italic;
  font-size: 40px;
  /* background-color: rgba(0, 0, 0, 0.495); */
}

input {
  margin-right: 20px;
  background-color: rgba(0, 0, 0, 0.801);
  color: aliceblue;
  border-radius: 4px;
}

button {
  background-color: rgba(0, 0, 0, 0.801);
  color: aliceblue;
  border-radius: 4px;
  margin-right: 20px;
}
button:hover,
button:focus {
  background-color: rgb(0, 0, 0);
}
